  getters: {
    getBreweryById: (state, getters) => (id) => {
      return state.breweries.find(brewery => brewery.breweryId === id);
    },
    getBeerForBrewery: (state, getters) => (id) => {
      return state.beers.filter(beer => beer.brewery_id === id)
    },
    getBeerByID: (state,getters) => (id) => {
      return state.beers.find(beer => beer.beerId === id);
    },
    getReviewsByBeerId: (state,getters) => (id) => {
      return state.reviews.filter(review => review.beerID === id);
    },
    getAverageRatingForBeerId: (state,getters) => (id) => {
      let reviewsForBeer = state.reviews.filter(review => review.beerID === id)
      let rating = reviewsForBeer.reduce((sum, review) => {
        return parseInt(sum) + parseInt(review.rating);
      }, 0);
      if(reviewsForBeer.length === 0) {
        return 0;
      }
      return Math.round(rating / reviewsForBeer.length);
    },
    getAverageRatingForBreweryId: (state,getters) => (id) => {
      let reviewsForBrewery = state.reviews.filter(review => review.brewery_id === id)
      let rating = reviewsForBrewery.reduce((sum, review) => {
        return parseInt(sum) + parseInt(review.rating);
      }, 0);
      if(reviewsForBrewery.length === 0) {
        return 0;
      }
      return Math.round(rating / reviewsForBrewery.length);
    },
    getNumberOfReviewsByBreweryId: (state,getters) => (id) => {
      let reviewsForBrewery = state.reviews.filter(review => review.brewery_id === id);
      return reviewsForBrewery.length;
    },
    getApproved: state => {
      return state.breweries.filter(brewery => brewery.is_approved)
    },
    getUnapproved: state => {
      return state.breweries.filter(brewery => !brewery.is_approved)
    },
    getBreweriesByUserId: (state,getters) => (id) => {
      return state.breweries.filter(brewery => brewery.owner === id);
    },
    getAllUSers: state => {
      return state.users
    },
    findUser: (state,getters) => (username) => {
      let user = null;
      user = state.users.find(user => user.username.toLowerCase() === username);
      if(user) {
        return user;
      } else {
        return 0;
      }
    },
    getUsernameById: (state,getters) => (id) => {
      return state.users.find(user => user.userId === id);
    },
    getUserIdByUsername: state => {
      let user = null;
      user = state.users.find(user => user.username === state.username);
      if(!user) {
         return 0;
      } else {
        return user.userId;
      }
    },
    getReviewsForUserId: (state,getters) => (id) => {
      return state.reviews.filter(review => review.userId === id);
    },
    getNextBreweryId: state => {
      let lastId = state.breweries[state.breweries.length-1].breweryId;
      return parseInt(lastId + 1);
    },
    getNextBeerId: state => {
      let lastId = state.beers[state.beers.length-1].beerId;
      return parseInt(lastId + 1);
    },
    getNextUserId: state => {
      let lastId = state.users[state.users.length-1].userId;
      return parseInt(lastId + 1);
    }
  },
  mutations: {
    SET_USERNAME (state, username) {
      state.username = username;
      state.loggedIn = true;
    },
    SET_ADMIN (state) {
      state.adminStatus = true;
    },
    SET_BREWER (state) {
      state.brewerStatus = true;
    },
    LOGOUT(state) {
      state.username = "";
      state.adminStatus = false;
      state.brewerStatus = false;
      state.loggedIn = false;
    },
    ADD_REVIEW (state, review) {
      state.reviews.push(review);
    },
    ADD_BREWERY (state,brewery) {
      state.breweries.push(brewery);
    },
    ADD_BEER (state,beer) {
      state.beers.push(beer);
    },
    ADD_USER (state, user) {
      state.users.push(user);
    },
    APPROVE_BREWERY(state, breweryToUpdate) {
      state.breweries.find(currentBrewery => {
        if(currentBrewery.breweryId===breweryToUpdate.breweryId) {
          currentBrewery.is_approved = true;
        }
      });
    },
    DELETE_BREWERY(state,id) {
      let breweriesWithoutBad = state.breweries.filter(brewery => {
        if(brewery.breweryId !== id) {
          return brewery;
        }
      });
      state.breweries = breweriesWithoutBad;
    },
    DELETE_REVIEW(state,id) {
      let reviewsWithoutBad = state.reviews.filter(review => {
        if(review.reviewId !== id) {
          return review;
        }
      });
      state.reviews = reviewsWithoutBad;
    },
    DELETE_BEER(state,id) {
      let beersWithout = state.beers.filter(beer => {
        if(beer.beerId !== id) {
          return beer;
        }
      });
      state.beers = beersWithout;
    }
  },
